import styled from 'styled-components';
import ButtonLong from '../common/ButtonLong';
import { useState, useEffect } from 'react';

const S = {
  Header: styled.h1`
    font-weight: bold;
    font-size: 24px;
    color: #000;
    margin-bottom: 60px;
  `,
  Info: styled.div`
    margin-bottom: 20px;
  `,
  Name: styled.div`
    margin-bottom: 20px;
  `,
  Input: styled.input`
    width: 322px;
    height: 40px;
    font-size: 15px;
    color: #000;
    border-bottom: 1px solid black;
    ::placeholder {
      font-weight: bold;
    }
  `,
  Label: styled.label`
    display: block;
    margin-bottom: 10px;
    font-weight: 600;
  `,
};

const InfoForm = ({ type, onSubmit }) => {
  const title = {
    login: '  ',
    register: '   ',
    PwChange: '     ',
    find: '     ',
  };
  const headline = title[type];

  const [btnOn, setBtnOn] = useState(false);
  const [data, setData] = useState({
    name: '',
    id: '',
    pw: '',
    current_pw: '',
    new_pw: '',
    new_pw2: '',
  });

  useEffect(() => {
    btnOnOff();
  }, [data]);

  const InputChange = (e) => {
    setData({
      ...data,
      [e.target.name]: e.target.value,
    });
  };

  const btnOnOff = () => {
    if (type === 'register') {
      if (!!data.name && !!data.id && data.pw.length > 5) {
        setBtnOn(true);
      } else {
        setBtnOn(false);
      }
    } else if (type === 'login') {
      if (!!data.id && data.pw.length > 5) {
        setBtnOn(true);
      } else {
        setBtnOn(false);
      }
    } else if (type === 'find') {
      if (!!data.name && !!data.id) {
        setBtnOn(true);
      } else {
        setBtnOn(false);
      }
    } else {
      if (
        !!data.current_pw &&
        data.new_pw.length > 5 &&
        data.new_pw === data.new_pw2
      ) {
        setBtnOn(true);
      } else {
        setBtnOn(false);
      }
    }
  };
  const btnSubmit = async (e) => {
    e.preventDefault();
    if (btnOn) {
      onSubmit(data);
    }
  };

  return (
    <>
      <S.Header>{headline}</S.Header>
      {['register', 'find'].includes(type) && (
        <S.Name>
          <S.Label htmlFor="name"></S.Label>
          <S.Input
            name="name"
            value={data.name}
            placeholder=" "
            type="text"
            onChange={InputChange}
          />
        </S.Name>
      )}
      {['register', 'login', 'find'].includes(type) && (
        <S.Info>
          <S.Label htmlFor="id"></S.Label>
          <S.Input
            name="id"
            value={data.id}
            placeholder=" "
            type="text"
            onChange={InputChange}
          />
        </S.Info>
      )}
      {['register', 'login'].includes(type) && (
        <S.Info>
          <S.Label htmlFor="pw"></S.Label>
          <S.Input
            name="pw"
            type="password"
            value={data.pw}
            placeholder="8~15  "
            maxLength={15}
            onChange={InputChange}
          />
        </S.Info>
      )}
      {type === 'PwChange' && (
        <S.Info>
          <S.Label htmlFor="current_pw"> </S.Label>
          <S.Input
            name="current_pw"
            value={data.current_pw}
            placeholder="  ."
            type="password"
            onChange={InputChange}
          />
        </S.Info>
      )}
      {type === 'PwChange' && (
        <S.Info>
          <S.Label htmlFor="new_pw"> </S.Label>
          <S.Input
            name="new_pw"
            value={data.new_pw}
            placeholder="  ."
            type="password"
            onChange={InputChange}
          />
        </S.Info>
      )}
      {type === 'PwChange' && (
        <S.Info>
          <S.Label htmlFor="new_pw2"> ()</S.Label>
          <S.Input
            name="new_pw2"
            value={data.new_pw2}
            placeholder="   ."
            type="password"
            onChange={InputChange}
          />
        </S.Info>
      )}

      <ButtonLong
        text={
          type === 'register'
            ? ''
            : type === 'login'
            ? ''
            : type === 'find'
            ? '  '
            : type === 'PwChange'
            ? ' '
            : ''
        }
        onClick={btnSubmit}
        active={btnOn}
      />
    </>
  );
};

export default InfoForm;
