import React from 'react';
import styled, { css } from 'styled-components';
import sideImg from '../../assets/img/logo-removebg-preview.png';
import Button from '../common/Button';
import SideMenuFooter from './SideMenuFooter';
import { useNavigate } from 'react-router-dom';
import { LoginDispatchContext } from '../../lib/context/LoginContext';
import { useContext } from 'react';
import { DeleteUser } from '../../lib/apis/DelateUser';

const A = {
  sidemenu: styled.div`
    z-index: 1000 !important;
    width: 100%;
    height: 100%;
    position: absolute;
    bottom: 0;
    align-self: flex-end;
    background-color: #fff;
    border-radius: 30px 30px 0 0;
    color: #767676;
    text-align: center;
    transition: bottom 0.35s;
    box-shadow: 0px 0px 30px #000;
  `,
  Name: styled.span`
    font-size: 20px;
    font-weight: bold;
  `,
  NameBox: styled.div`
    position: relative;
    padding: 10px;
    bottom: 80px;
  `,
  Sideimg: styled.div`
    width: 390px;
    height: 400px;
    margin-top: 50px;
    background-image: url(${sideImg});
    background-repeat: no-repeat;
    background-size: cover;
    background-position: -130px;
    opacity: 0.1;
  `,
  Sidebox: styled.div`
    position: absolute;
    top: 0px;
    width: 100%;
    height: 300px;
    margin-top: 50px;
    font-size: 15px;
  `,
  SideTitle: styled.h2`
    font-weight: bold;
    font-size: 30px;
    color: #05194d;
    line-height: 50px;
  `,
  MenuSpan: styled.span`
    color: #05194d;
    font-weight: bold;
  `,
  MenuList: styled.div`
    width: 120px;
    color: #000;
    display: block;
    font-size: 15px;
    opacity: 0.8;
    padding: 10px 0;
    text-align: center;
    margin: 20px auto;
    border-bottom: 1px solid black;
    cursor: pointer;
  `,
  StyledButton: styled.button`
    margin: 0 5px;
    width: 85px;
    height: 32px;
    border-radius: 10px;
    font-size: 12px;
    font-weight: bold;
    background-color: #e2e2e2;
    cursor: pointer;
    ${(props) =>
      props.active === false &&
      css`
        background-color: #f7f7f7;
        color: #000;
        :hover {
        }
        pointer-events: none;
      `}
  `,
};

const SideMenuModal = ({ sideOn }) => {
  const user = sessionStorage.getItem('username');
  const { logout } = useContext(LoginDispatchContext);
  const navigate = useNavigate();

  const UserDelete = async (id) => {
    try {
      const response = await DeleteUser(id);
      if (response.success) {
        alert(' .');
        logout();
        navigate('/');
      } else {
        alert('   .');
      }
    } catch (error) {
      console.error(error);
    }
  };

  const handleLogout = () => {
    logout();
    navigate('/');
  };
  const handleChange = () => {
    navigate('/change');
  };
  const handleDelete = () => {
    if (
      window.confirm(
        '  ?\n      .',
      )
    ) {
      UserDelete(sessionStorage.getItem('id'));
    } else {
      return;
    }
  };

  return (
    <A.sidemenu sideOn={sideOn}>
      <A.Sideimg />
      <A.NameBox>
        <A.Name>{user}</A.Name>  .
        <br />
        <br />
        <Button text={''} onClick={handleLogout} />
        <A.StyledButton onClick={handleChange}> </A.StyledButton>
        <Button text={''} onClick={handleDelete} />
      </A.NameBox>
      <A.Sidebox>
         ,  
        <A.SideTitle>:</A.SideTitle>
        <A.MenuList
          onClick={() => {
            navigate('/intro');
          }}
        >
          <A.MenuSpan></A.MenuSpan> 
        </A.MenuList>
        <A.MenuList
          onClick={() => {
            navigate('/commu');
          }}
        >
           <A.MenuSpan></A.MenuSpan>
        </A.MenuList>
        <A.MenuList
          onClick={() => {
            navigate('/car', { state: ' ' });
          }}
        >
          <A.MenuSpan></A.MenuSpan> 
        </A.MenuList>
        <A.MenuList
          onClick={() => {
            navigate('/map');
          }}
        >
           <A.MenuSpan></A.MenuSpan> 
        </A.MenuList>
      </A.Sidebox>
      <SideMenuFooter />
    </A.sidemenu>
  );
};

export default SideMenuModal;
